import React, { Suspense } from 'react';
import Navigationcode from './navigationcode';
import { useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import { useNavigate, useLocation } from 'react-router-dom';
import './tempmodelstyle.css';
import './create.css';
import './steps.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilePen } from '@fortawesome/free-solid-svg-icons';
import Coloranimationcode from './coloranimationcode';
import rPhotoG2 from '../img/rPhoto2.gif';

const Resumeform = React.lazy(() => import('./resumeform'));
const Footer = React.lazy(() => import('./footer'));

function Create() {
	// all navigations
  const { goCreate, goHome, goSteps, goTemplates, goResumeForm, scrollToTop, scrollToBottom , goResumeButton} = Navigationcode();
  // Logo animation color
  const bgColor = Coloranimationcode(['#fecb88', 'white'], 1000);

  return (
    <>
      <Helmet>
        <title>RESUMY. CREATE - Complete your resume details</title>
        <meta
          name="description"
          content="Build professional resumes effortlessly with our free online resume maker. free Customizable fresher, intermediate, professional templates and download in Seconds!"
        />
        <meta property="og:title" content="Resume Maker" />
        <meta property="og:description" content="Create resumes effortlessly with modern templates." />
        <meta property="og:url" content="https://mukeshgit9.github.io/resumy/#/create" />
      </Helmet>

      {/* Logo */}
      <div className="Container-fluid Container-fluid-no-overflow logo-header-outer">
        <div className="row justify-content-center align-items-center">
          <div className="col-11 col-sm-11 col-md-10 col-lg-10 col-xl-10">
            <h1>
              RESU<span style={{ color: bgColor }} onClick={goHome}>MY.</span>
            </h1>
          </div>
        </div>
      </div>

      {/* Complete Section */}
      <div className="Container-fluid Container-fluid-no-overflow">
        <div className="row justify-content-center align-items-center complete-heading-box">
          <div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 complete-rs-heading">
            <h1>Complete Your Resume Details</h1>
            <p>Just a few steps away from your perfect resume! Fill in your details below.</p>
            <button onClick={goResumeForm}>
              Fill Details <FontAwesomeIcon icon={faFilePen} />
            </button>
          </div>
          <div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 resume-hd-box-img create-img-box">
            <img src={rPhotoG2} className="d-block w-100" alt="resume-cover-photo" loading="lazy" />
          </div>
        </div>
      </div>

      {/*steps sub section-----------------------------------------------------------*/}
      <div className="Container-fluid Container-fluid-no-overflow ">
        <div className="row justify-content-around align-items-center create-steps-div" id="steps-target">
          <div   className=" col-4 col-sm-4 col-md-3 col-lg-3 col-xl-3  ">
            <div className="create-steps-icon-div steps-count-icon-write" onClick={goResumeForm}></div>
              <h3 className="create-steps-heading" >FILL</h3>
          </div>
          <div   className=" col-4 col-sm-4 col-md-3 col-lg-3 col-xl-3  ">
             <div className="create-steps-icon-div steps-count-icon-temp" onClick={goResumeButton}></div>
             <h3 className="create-steps-heading" >CHOOSE</h3>
          </div>
          <div   className=" col-4 col-sm-4 col-md-3 col-lg-3 col-xl-3  ">
              <div className="create-steps-icon-div steps-count-icon-download" onClick={goResumeButton}></div>
              <h3  className="create-steps-heading">DOWNLOAD</h3>
          </div>
        </div>

      </div>
        {/* Resume Form --------------------------------------------------------*/}
       <Suspense fallback={<div className="loader"></div>}>
        <Resumeform />
      </Suspense>

      {/* Footer --------------------------------------------------------*/}
      <Suspense fallback={<div className="loader"></div>}>
        <Footer />
      </Suspense>
    </>
  );
}

export default Create;
